import * as d3 from "d3";
import { mapState, mapMutations, mapGetters } from "vuex";
import { regressionLinear } from "d3-regression";

export default {
  name: "Scatterplot",
  data: () => ({
    progressBarValue: 0,
    width: 800,
    height: 500,
    margin: { top: 40, right: 40, bottom: 60, left: 80 },
    svg: Object,
    // tooltip: d3.select("#tooltip"),
    selectedCountry: Object,
    // pointRadius: 5,
    style: {
      highlightPointColor: "#FF3D00",
      vizBackground: "#fff",
      circleRadius: 6,
      circleColors: {
        default: "#444",
        pushedBack: "#ddd",
        selectedCountryUnactiveRegion: '#555'
      },
      circleOpacity: {
        default: 0.7,
        pushedBack: 0.5,
        disappearing: 0,
        highlighted: 0.9,
        decreased: 0.2
      },
      circleStroke: {
        color: "#9d9d9d",
        highlighted: '#222',
        width: 1,
        thick: 3
      },

      // circleRadius: {
      //     default: 5,
      //     pop: 1,
      //     obesity:
      // }
    },
  }),
  computed: {
    ...mapState([
      "dataset",
      "dataIsReady",
      "selectedPlotPoint",
      "current_state",
      "color_code",
    ]),
    ...mapGetters(["get_region_list"]),
    filteredDataset() {
      return this.dataset.filter( d => {
        return this.current_state.selected_region.indexOf(d['region']) >= 0;
      });
    },
    isReady() {
      return typeof this.dataset != "function" && this.dataset.length
        ? true
        : false;
    },
    svgHeight() {
      return this.height + this.margin.top + this.margin.bottom;
    },
    svgWidth() {
      return this.width + this.margin.left + this.margin.right;
    },
    xScale() {
      const xScale = d3
        .scaleLinear()
        .domain([0, d3.max(this.dataset.map((value) => value.nutrient))])
        .range([0, this.width])
        .nice();
      return xScale;
    },
    yScale() {
      const yScale = d3
        .scaleLinear()
        .domain([0, d3.max(this.dataset.map((value) => value.disease))])
        .range([this.height, 0])
        .nice();
      return yScale;
    },
    populationRadiusScale() {
      return d3.scaleSqrt([0, 5e8], [0, this.width / 24]);
    },
    colorMap() {
      const colorMap = d3
        .scaleOrdinal()
        .domain(this.get_region_list)
        .range(this.color_code);
      return colorMap;
    },
    regression() {
      const regression = regressionLinear()
        .x((d) => d.nutrient)
        .y((d) => d.disease);

      const regressionLine = regression(this.dataset);
      return regression;
    },
    zoom() {
      const zoom = d3
        .zoom()
        .scaleExtent([1, 8])
        .translateExtent([
          [0, 0],
          [this.svgWidth, this.svgHeight],
        ])
        .on("zoom", this.zoomed);
      return zoom;
    },
    tooltip() {
      return d3.select("#tooltip");
    },
  },
  mounted() {
    if (this.isReady) this.render();
  },
  watch: {
    // dataIsReady: function() {
    //     if (this.isReady) this.render();
    // },
    selectedCountry: function () {
      // this.$store.commit('updateSelectedPlotPoint', {selectedPlotPoint: this.selectedCountry});
      this.$store.dispatch("getCountryDetails");
    },
    dataset: function () {
      if (!this.isReady) return;

      // if (this.isReady) {
      //     this.render();
      //     // if (this.current_state.size !== null) {
      //     //     this.updateCircleSize();
      //     // }
      //     // this.manageCirclesStyles();
      // }

      // console.log(d3.select("svg > g").empty())

      if (!d3.select("svg > g").empty()) {
        this.updateScatter();
      }

      if (this.current_state.regression_line) {
        this.addRegressionLine();
      } else {
        this.removeRegressionLine();
      }
    },
    "current_state.regression_line": function () {
      if (this.current_state.regression_line) {
        this.addRegressionLine();
      } else {
        this.removeRegressionLine();
      }
    },
    "current_state.size": function () {
      // console.log("changing size...");
      this.updateCircleSize();
    },
    "current_state.hovered_region": function () {
      // console.log("changing hovered...");
      this.updateCircleColorOnHover();
    },
    "current_state.selected_region": function () {
      this.manageCirclesStyles();
    },
    "current_state.country": function () {
      // console.log("country selected...");
      this.manageCirclesStyles();
      // this.updateCircleColorOnHover();
      // this.updateSelectedCountryPoint();
    },
  },
  methods: {
    ...mapMutations(["updateSelectedPlotPoint"]),
    zoomed: function ({ transform }) {
      const zx = transform.rescaleX(this.xScale);
      const zy = transform.rescaleY(this.yScale);

      // console.log(zy.domain(), zx.domain())

      d3.select(".highlight-circle").style("display", "none");

      d3.select(".voronoiWrapper")
        .selectAll("path")
        .attr("d", (d, i) => {
          return this.voronoi(zx, zy, i);
        })
        .on("mouseover", (event, d) => {
          this.highlightPoint(zx, zy, d, event);
          this.showTooltip(event, d);
        })
        .on("mouseleave", (event, d) => {
          this.hideTooltip();
          this.removeHighlightPoint(zx, zy, d);
        });

      d3.select("g.xAxis").call(d3.axisBottom(zx));

      d3.select("g.yAxis").call(d3.axisLeft(zy));

      d3.select("g.scatterCircles")
        .selectAll("circle")
        .attr("cx", (d) => zx(d.nutrient))
        .attr("cy", (d) => zy(d.disease));
    },
    drawSvg: function () {
      this.svg = d3
        .select("#scatterplot")
        .append("svg")
        .attr("preserveAspectRatio", "xMinYMin meet")
        .attr("viewBox", `0 0 ${this.svgWidth} ${this.svgHeight}`)
        // .attr("viewBox", [0, 0, this.width, this.height])
        .classed("svg-content", true)
        // .style('background', 'pink')
        .append("g")
        .attr(
          "transform",
          "translate(" + this.margin.left + "," + this.margin.top + ")"
        );

      this.svg
        .append("defs")
        .append("svg:clipPath")
        .attr("id", "clip")
        .append("svg:rect")
        .attr("id", "clip-rect")
        .attr("x", "0")
        .attr("y", "0")
        .attr("width", this.width)
        .attr("height", this.height);
    },
    render: function () {
      // this.hideTooltip = false;
      // console.log("---render viz---");
      d3.select("#scatterplot svg").remove();

      this.drawSvg();

      this.svg.call(this.zoom).call(this.zoom.transform, d3.zoomIdentity);
      // .on("mousedown.zoom", null)
      // .on("touchstart.zoom", null)
      // .on("touchmove.zoom", null)
      // .on("touchend.zoom", null);

      // create axes
      this.svg
        .append("g")
        .attr("class", "axis xAxis")
        .attr("transform", "translate(0," + this.height + ")")
        .call(d3.axisBottom(this.xScale));

      this.svg
        .append("g")
        .attr("class", "axis yAxis")
        .call(d3.axisLeft(this.yScale));

      // add x axis label
      this.svg
        .append("text")
        .attr("class", "label x-label")
        .attr("text-anchor", "end")
        .attr("x", this.width + 5)
        .attr("y", this.height + 35)
        .text(
          `${this.current_state.nutrient.label} - ${this.current_state.nutrient.food_group} (${this.current_state.nutrient.serving_size})`
        );

      // add y axis label
      this.svg
        .append("text")
        .attr("class", "label y-label")
        .attr("text-anchor", "end")
        .attr("y", -60)
        .attr("dx", "0.2em")
        .attr("transform", "rotate(-90)")
        .text(`${this.current_state.disease.label} - Prevalence per 100k`);

      // this.current_state.disease.label - name of disease
      // this.current_state.nutrient.label - name of nutrient
      // this.current_state.nutrient.food_group - name of food group
      // this.current_state.nutrient.serving_size - measurement

      // console.log( d3.extent(this.dataset.map(d => d[sizeByValue]) ))

      this.svg
        .append("g")
        .attr("class", "scatterCircles")
        .selectAll("circle")
        .data(this.dataset)
        .enter()
        .append("circle")
        .attr("id", (d) => `circle_${d["iso"]}`)
        .attr("cx", (d) => this.xScale(d.nutrient))
        .attr("cy", (d) => this.yScale(d.disease))
        .attr("r", (d) => this.style.circleRadius)
        .attr("stroke", d => {
          return this.current_state.country && d.country === this.current_state.country ? this.style.circleStroke.highlighted : this.style.circleStroke.color
        })
        .attr("stroke-width", d => {
          return this.current_state.country && d.country === this.current_state.country ? this.style.circleStroke.thick : this.style.circleStroke.width
        })
        .attr("fill", (d) => {
          if (this.current_state.country) {
            if (d.country === this.current_state.country) {
              if( this.current_state.selected_region.indexOf(d['region']) === -1 || this.current_state.hovered_region ) {
                return this.style.circleColors.selectedCountryUnactiveRegion
              } else {
                  return this.colorMap(d["region"]);
              }
              
            } else {
              return this.colorMap(d["region"]); //'#999'
            }
          } else {
            return this.colorMap(d["region"]);
          }
        })
        .style("opacity", (d) => {
          if (this.current_state.country) {
            if (d.country === this.current_state.country) {
              return this.style.circleOpacity.highlighted;
            } else {
              return this.style.circleOpacity.pushedBack;
            }
          } else {
            return this.style.circleOpacity.default;
          }

          
        })
        .attr("clip-path", "url(#clip)");


      // append circles highlight
      // this.svg.append('circle')
      //     .attr('class', 'highlight-circle')
      //     .attr('r', d => {
      //         return this.calculateCircleSize(d) + 3
      //     }) // slightly larger than our points
      //     .style('fill', 'none')
      //     .style('stroke', this.style.highlightPointColor)
      //     .style('stroke-width', '4pt')
      //     .style('display', 'none');

      this.svg
        .append("g")
        .attr("class", "voronoiWrapper")
        .selectAll("path")
        .data(this.dataset)
        .join("path")
        .attr("id", (d) => d["iso"])
        // .style("opacity", 0.5)
        // .attr("stroke", "#098")
        // .attr("stroke", "none")
        .attr("fill", "none")
        .style("pointer-events", "all")
        .attr("d", (d, i) => {
          return this.voronoi(this.xScale, this.yScale, i);
        })
        .on("mouseover", (event, d) => {
          // mouseenter ?
          this.highlightPoint(this.xScale, this.yScale, d, event);
          this.showTooltip(event, d);
        })
        .on("mouseleave", (event, d) => {
          this.hideTooltip();
          this.removeHighlightPoint(this.xScale, this.yScale, d);
        });

      this.svg
        .append("line")
        .attr("class", "regression")
        // .datum(this.regression(this.dataset))
        // .attr("x1", d => this.xScale(d[0][0]))
        // .attr("x2", d => this.xScale(d[1][0]))
        // .attr("y1", d => this.yScale(d[0][1]))
        // .attr("y2", d => this.yScale(d[1][1]))
        .style("display", "none");

        this.updateCircleSize();
    },
    updateScatter: function () {
      // console.log("updating...", this.dataset);
      // console.log(this.xScale.domain(), this.yScale.domain())
      this.updateCircleSize();

      let dataset = this.dataset; //filteredDataset

      // update x axis
      this.svg
        .select(".axis.xAxis")
        .transition()
        .duration(500)
        .call(d3.axisBottom(this.xScale));

      // update y axis
      this.svg
        .select(".axis.yAxis")
        .transition()
        .duration(500)
        .call(d3.axisLeft(this.yScale));

      // update x axis text
      this.svg
        .select(".label.x-label")
        .transition()
        .duration(500)
        .text(
          `${this.current_state.nutrient.label} - ${this.current_state.nutrient.food_group} (${this.current_state.nutrient.serving_size})`
        );

      // update y axis text
      this.svg
        .select(".label.y-label")
        .transition()
        .duration(500)
        .text(`${this.current_state.disease.label} - Prevalence per 100k`);

      // update circles
      let sizeValue = this.current_state.size.value;

      this.svg
        .select("g.scatterCircles")
        .selectAll("circle")
        .data(dataset)
        .transition()
        .duration(500)               
        .attr("id", (d) => `circle_${d["iso"]}`)
        .attr("cx", (d) => {
          return this.xScale(d.nutrient);
        })
        .attr("cy", (d) => {
          return this.yScale(d.disease);
        })
        .attr("r", (d) => {
          if (sizeValue == null) {
            return this.style.circleRadius;
          } else if (sizeValue == "pop") {
            return d[sizeValue] != 999
              ? this.populationRadiusScale(d["pop"])
              : 0; //d[sizeValue] / 10000000 : 0;
          } else if (sizeValue == "obesity") {
            return d[sizeValue] != 999 ? d[sizeValue] / 4 : 0;
          } else if (sizeValue == "overweight") {
            return d[sizeValue] != 999 ? d[sizeValue] / 10 : 0;
          } else if (sizeValue == "underweight") {
            return d[sizeValue] != 999 ? d[sizeValue] : 0;
          }
        })
        .attr("stroke", d => {
          return this.current_state.country && d.country === this.current_state.country ? this.style.circleStroke.highlighted : this.style.circleStroke.color
        })
        .attr("stroke-width", d => {
          return this.current_state.country && d.country === this.current_state.country ? this.style.circleStroke.thick : this.style.circleStroke.width
        })
        .attr("fill", (d) => {
          if (this.current_state.country) {
            if (d.country === this.current_state.country) {

              if( this.current_state.selected_region.indexOf(d['region']) === -1 || this.current_state.hovered_region ) {
                return this.style.circleColors.selectedCountryUnactiveRegion
              } else {
                  return this.colorMap(d["region"]);
              }
              
            } 
            else {
              if( this.current_state.selected_region.indexOf(d['region']) === -1 || this.current_state.hovered_region ) {
                return this.style.circleColors.pushedBack
              } else {
                  return this.colorMap(d["region"]);
              }
            }
          } else {
              if( this.current_state.selected_region.indexOf(d['region']) === -1 || this.current_state.hovered_region ) {
                return this.style.circleColors.pushedBack
              } else {
                  return this.colorMap(d["region"]);
              }
          }
        })
        .style("opacity", (d) => {
          if (this.current_state.country) {
            if (d.country === this.current_state.country) {
              return this.style.circleOpacity.highlighted;
            } else {
              return this.style.circleOpacity.pushedBack;
            }
          } else {
            return this.style.circleOpacity.default;
          }
        
        })
        .attr("clip-path", "url(#clip)")


      this.svg.selectAll(".voronoiWrapper").remove();

      this.svg
        .append("g")
        .attr("class", "voronoiWrapper")
        .selectAll("path")
        .data(dataset)
        .join("path")
        .attr("id", (d) => d["iso"])
        // .style("opacity", 0.5)
        // .attr("stroke", "#ff1493")
        // .attr("stroke", "none")
        .attr("fill", "none")
        .style("pointer-events", "all")
        .attr("d", (d, i) => {
          return this.voronoi(this.xScale, this.yScale, i);
        })
        .on("mouseover", (event, d) => {
          // mouseenter ?
          this.highlightPoint(this.xScale, this.yScale, d, event);
          this.showTooltip(event, d);

        })
        .on("mouseleave", (event, d) => {
          this.hideTooltip();
          this.removeHighlightPoint(this.xScale, this.yScale, d);
        });

      //                 .append("g")
      //   .style("font", "10px sans-serif")
      // .selectAll("text")
      // .data(cells)
      // .join("text")
      //   .each(function([[x, y], cell]) {
      //     const [cx, cy] = d3.polygonCentroid(cell);
      //     const angle = (Math.round(Math.atan2(cy - y, cx - x) / Math.PI * 2) + 4) % 4;
      //     d3.select(this).call(angle === 0 ? orient.right
      //         : angle === 3 ? orient.top
      //         : angle === 1 ? orient.bottom
      //         : orient.left);
      //   })
      //   .attr("transform", ([d]) => `translate(${d})`)
      //   .attr("display", ([, cell]) => -d3.polygonArea(cell) > 2000 ? null : "none")
      //   .text((d, i) => i);
    },
    addRegressionLine: function () {
      d3.select("line.regression")
        .style("display", "block")
        .datum(this.regression(this.dataset))
        .attr("x1", (d) => 0)
        .attr("x2", (d) => 0)
        .attr("y1", (d) => this.yScale(d[0][1]))
        .attr("y2", (d) => 0);

      d3.select("line.regression")
        .datum(this.regression(this.dataset))
        .transition()
        .duration(500)
        .attr("x1", (d) => this.xScale(d[0][0]))
        .attr("x2", (d) => this.xScale(d[1][0]))
        .attr("y1", (d) => this.yScale(d[0][1]))
        .attr("y2", (d) => this.yScale(d[1][1]))
        .style("display", "block")
        .attr("clip-path", "url(#clip)");
    },
    removeRegressionLine: function () {
      d3.select("line.regression").style("display", "none");
    },
    resetZoom: function () {
      this.svg
        .transition()
        .duration(750)
        .call(this.zoom.transform, d3.zoomIdentity);
    },
    updateCircleSize: function () {
      // console.log('updateCircleSize')
      if(!this.current_state.size.hasOwnProperty('value')) return;

      let sizeValue = this.current_state.size.value;

      // d3.selectAll('*').transition().stop();

      d3.selectAll(".v-input").style("pointer-events", "none");

      d3.select("g.scatterCircles")
        .selectAll("circle")
        .transition()
        .duration(500)
        .attr("r", (d) => {
          if (sizeValue == null) {
            return this.style.circleRadius;
          } else if (sizeValue == "pop") {
            return d[sizeValue] != 999
              ? this.populationRadiusScale(d["pop"])
              : 0; //d[sizeValue] / 10000000 : 0;
          } else if (sizeValue == "obesity") {
            return d[sizeValue] != 999 ? d[sizeValue] / 4 : 0;
          } else if (sizeValue == "overweight") {
            return d[sizeValue] != 999 ? d[sizeValue] / 10 : 0;
          } else if (sizeValue == "underweight") {
            return d[sizeValue] != 999 ? d[sizeValue] : 0;
          }
        });

      setTimeout((d) => {
        d3.selectAll(".v-input").style("pointer-events", "all");
      }, 510);
    },
    updateCircleColorOnHover: function () {
      // d3.selectAll('*').transition().stop();

      // when a checkbox is hovered, push all circles not in this region to the back (lower opacity)
      let hovered_region = this.current_state.hovered_region;
      let selected_region = this.current_state.selected_region;
      let opacity = this.style.circleOpacity;
      let color = this.style.circleColors;
      let stroke = this.style.circleStroke;
      let selected_country = this.current_state.country;
      let vueThis = this;

      d3.select("g.scatterCircles")
        .selectAll("circle")
        .transition()
        .duration(700)
        .style("opacity", function (d) {
          // if there is no hovered region, all regions are default
          if (hovered_region === false) {
            if (selected_country && d.country === selected_country) {
              // this is the selected country
              // d3.select(this).raise();
              return opacity.highlighted;
            } else {
              // if the region is active
              if (selected_region.indexOf(d.region) >= 0) {
                return selected_country ? opacity.pushedBack : opacity.default;
              } else {
                // the region is not active
                return opacity.pushedBack; 
              }
            }
          } else {
            // a region is hovered
            if (selected_country && d.country === selected_country) {
              // this is the selected country
               if (d.region === hovered_region) {
                  d3.select(this).raise();
               }
              
              return opacity.highlighted;
            } else {
              // this is the hovered region
              if (d.region === hovered_region) {
                // the region is active
                if (selected_region.indexOf(d.region) >= 0) {
                  d3.select(this).raise();
                  // return opacity.default;
                  return opacity.default;
                } else {
                  // the region is not active
                  return opacity.pushedBack;
                }
              } else {
                // the region is not the hovered one
                return opacity.pushedBack;
              }
            }
          }
        })
        .attr("fill", (d) => {
          // if a region is not hovered
          if (hovered_region === false) {
            // restore the color of the selected country
            if (selected_country && d.country === selected_country) {
              
              if( selected_region.indexOf(d['region']) === -1 ) {
                return color.selectedCountryUnactiveRegion
              } else {
                  return vueThis.colorMap(d["region"]);
              }
              
            } else {
              // if the region's checkbox is on
              if (selected_region.indexOf(d.region) >= 0) {
                // restore the color coding
                return vueThis.colorMap(d["region"]);
              } else {
                // make the circles gray
                return color.pushedBack;
              }
            }
          } else {
            // a region is hovered and the current country matches a selected country from the dropdown 
            if (selected_country && d.country === selected_country) {
              // this is the selected country
              // d3.select(this).raise();
                if(selected_region.indexOf(d.region) >= 0 && d.region === hovered_region) {
                  return vueThis.colorMap(d["region"]);
                } else {
                  return color.selectedCountryUnactiveRegion
                }
            } else {
              // this is the hovered region
              if (d.region === hovered_region) {
                // the region is active
                if (selected_region.indexOf(d.region) >= 0) {
                  // return opacity.default;
                  return vueThis.colorMap(d["region"]);
                } else {
                  // the region is not active
                  return color.pushedBack;
                }
              } else {
                // the region is not the hovered one
                return color.pushedBack;
              }
            }
          }
        })
        .attr("stroke-width", (d) => {
          if (selected_country && d.country === selected_country) {
            return '3pt';
          } else {
            return this.style.circleStroke.width;
          }
        }).attr("stroke", d => {
          return selected_country && d.country === selected_country ? stroke.highlighted : stroke.color
        })

      // if country is not selected, use logic below

      // if no region is hovered -> all regions are the same
      // if region is hovered
      // the region is the one that is hovered
      // the region is active -> this region pops, others go back
      // the region is not active -> this region does not pop
      // the region is not the one that is hovered
      // the region is active -> the region is pushed back
      // the reigon is not active -> the region is pushed back
    },
    manageCirclesStyles: function () {
      // when a region checkbox is clicked, it changes the  colors of the circles accordingly
      let opacity = this.style.circleOpacity;
      let color = this.style.circleColors;
      let stroke = this.style.circleStroke;
      let selected_country = this.current_state.country;

      let selectedRegions = this.current_state.selected_region.filter(
        (region) => {
          return region !== null;
        }
      );

      d3.select("g.scatterCircles")
        .selectAll("circle")
        .transition()
        .duration(1000)
        .style("opacity", function (d) {
          // if this region's checkbox is deselected
          if (selectedRegions.indexOf(d.region) < 0) {
            // the selected country is in a deselected region
            if (selected_country && d.country === selected_country) {
              d3.select(this).raise();
              return opacity.highlighted;
            } else {
              // country not selected or not from this region
              return opacity.pushedBack;
            }
          } 
          // if this region's checkbox is selected
          else {
            // add more opacity to selected country
            if (selected_country && d.country === selected_country) {
              d3.select(this).raise();
              return opacity.highlighted;
            } else {
              return opacity.default;
            }
          }
        })
        .attr("fill", (d) => {
          if (selectedRegions.length === 0) {
            // there are no selected regions
            return color.default;
          } else {
            // there are selected regions but this region is not selected
            if (selectedRegions.indexOf(d.region) < 0) {
              // and there is a highlighted country
              if (selected_country && d.country === selected_country) {
                // selected country from this region
               
                  return color.selectedCountryUnactiveRegion
               
              } else {
                return color.pushedBack;
              }
            } else {
              // this region is selected
              return this.colorMap(d["region"]);
            }
          }
        })
        .attr("stroke-width", (d) => {
          if (selected_country && d.country === selected_country) {
            return '3pt';
          } else {
            return this.style.circleStroke.width;
          }
        }).attr("stroke", d => {
          return selected_country && d.country === selected_country ? stroke.highlighted : stroke.color
        })
    },
    // updateSelectedCountryPoint: function () {
    //   let opacity = this.style.circleOpacity;
    //   let color = this.style.circleColors;
    //   let selected_country = this.current_state.country;

    //   //     if (selected_country && c.country === selected_country) {
