    voronoi(x, y, i) {
      return d3.Delaunay.from(
        this.dataset,
        (d) => x(d.nutrient),
        (d) => y(d.disease)
      )
        .voronoi([0, 0, this.width, this.height])
        .renderCell(i);
    },
    highlightPoint(x, y, d, event) {
