    removeHighlightPoint(x, y, d) {

      d3.select("g.scatterCircles")
        .selectAll("circle")
       
        // .attr("stroke", this.style.circleStroke.color)
        // .attr("stroke-width", this.style.circleStroke.width);
        .attr("stroke", d => {
          return this.current_state.country && d.country === this.current_state.country ? this.style.circleStroke.highlighted : this.style.circleStroke.color
        })
        .attr("stroke-width", d => {
          return this.current_state.country && d.country === this.current_state.country ? this.style.circleStroke.thick : this.style.circleStroke.width
        }).style("opacity", d => {
          if(this.current_state.country && d.country === this.current_state.country) {
            this.style.circleOpacity.highlighted
          } else {
            return this.current_state.country ? this.style.circleOpacity.pushedBack : this.style.circleOpacity.default
          }
        })


